import { FC, useState, useEffect } from 'react';
import { Save, AlertCircle, Eye, EyeOff, Sparkles, Key, Check, X } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import toast from 'react-hot-toast';

// API   
export class AIApiKeyService {
  private static STORAGE_KEY = 'ai_api_keys';

  static getKeys(): Record<string, string> {
    try {
      const stored = localStorage.getItem(this.STORAGE_KEY);
      return stored ? JSON.parse(stored) : {};
    } catch {
      return {};
    }
  }

  static setKey(provider: string, key: string): void {
    const keys = this.getKeys();
    keys[provider] = key;
    localStorage.setItem(this.STORAGE_KEY, JSON.stringify(keys));
  }

  static removeKey(provider: string): void {
    const keys = this.getKeys();
    delete keys[provider];
    localStorage.setItem(this.STORAGE_KEY, JSON.stringify(keys));
  }

  static getKey(provider: string): string | undefined {
    return this.getKeys()[provider];
  }

  //   
  static getDefaultModel(provider: string): string | undefined {
    try {
      const stored = localStorage.getItem(`ai_default_model_${provider}`);
      return stored || undefined;
    } catch {
      return undefined;
    }
  }

  static setDefaultModel(provider: string, model: string): void {
    localStorage.setItem(`ai_default_model_${provider}`, model);
  }
}

const AISettings: FC = () => {
  const [apiKeys, setApiKeys] = useState<Record<string, string>>({
    gemini: '',
    openai: '',
    claude: ''
  });
  
  const [defaultModels, setDefaultModels] = useState<Record<string, string>>({
    gemini: 'gemini-2.5-flash',
    openai: 'gpt-4-turbo',
    claude: 'claude-3-sonnet-20240229'
  });

  const [showKeys, setShowKeys] = useState<Record<string, boolean>>({
    gemini: false,
    openai: false,
    claude: false
  });

  const [loading, setLoading] = useState(false);
  const [testingProvider, setTestingProvider] = useState<string | null>(null);
  const [testResults, setTestResults] = useState<Record<string, boolean>>({});

  //  
  const providers = [
    { 
      id: 'gemini', 
      name: 'Google Gemini', 
      icon: '',
      models: [
        { id: 'gemini-2.5-flash', name: 'Gemini 2.5 Flash ()' },
        { id: 'gemini-2.5-pro', name: 'Gemini 2.5 Pro' },
        { id: 'gemini-2.0-flash', name: 'Gemini 2.0 Flash' },
        { id: 'gemini-1.5-flash', name: 'Gemini 1.5 Flash ( )' },
        { id: 'gemini-1.5-pro', name: 'Gemini 1.5 Pro ( )' }
      ],
      apiUrl: 'https://aistudio.google.com/app/apikey',
      description: 'Google AI Studio  API  '
    },
    { 
      id: 'openai', 
      name: 'OpenAI', 
      icon: '',
      models: [
        { id: 'gpt-4-turbo', name: 'GPT-4 Turbo' },
        { id: 'gpt-4', name: 'GPT-4' },
        { id: 'gpt-3.5-turbo', name: 'GPT-3.5 Turbo' }
      ],
      apiUrl: 'https://platform.openai.com/api-keys',
      description: 'OpenAI Platform API   ()'
    },
    { 
      id: 'claude', 
      name: 'Anthropic Claude', 
      icon: '',
      models: [
        { id: 'claude-3-opus-20240229', name: 'Claude 3 Opus' },
        { id: 'claude-3-sonnet-20240229', name: 'Claude 3 Sonnet' },
        { id: 'claude-3-haiku-20240307', name: 'Claude 3 Haiku' }
      ],
      apiUrl: 'https://console.anthropic.com/',
      description: 'Anthropic Console API   ()'
    }
  ];

  //      
  useEffect(() => {
    const savedKeys = AIApiKeyService.getKeys();
    const savedModels: Record<string, string> = {};
    
    providers.forEach(provider => {
      const savedModel = AIApiKeyService.getDefaultModel(provider.id);
      if (savedModel) {
        savedModels[provider.id] = savedModel;
      }
    });

    setApiKeys(prev => ({
      ...prev,
      ...savedKeys
    }));

    if (Object.keys(savedModels).length > 0) {
      setDefaultModels(prev => ({
        ...prev,
        ...savedModels
      }));
    }
  }, []);

  // API  
  const handleSave = async () => {
    setLoading(true);
    try {
      //    
      Object.entries(apiKeys).forEach(([provider, key]) => {
        if (key) {
          AIApiKeyService.setKey(provider, key);
        } else {
          AIApiKeyService.removeKey(provider);
        }
      });

      //   
      Object.entries(defaultModels).forEach(([provider, model]) => {
        AIApiKeyService.setDefaultModel(provider, model);
      });

      toast.success('AI API  .');
    } catch (error) {
      toast.error('    .');
    } finally {
      setLoading(false);
    }
  };

  // API  
  const handleTestKey = async (providerId: string) => {
    const key = apiKeys[providerId];
    if (!key) {
      toast.error('API  .');
      return;
    }

    setTestingProvider(providerId);
    try {
      //  API  
      let testUrl = '';
      let headers: HeadersInit = {};
      let body = {};

      if (providerId === 'gemini') {
        testUrl = `https://generativelanguage.googleapis.com/v1beta/models?key=${key}`;
        const response = await fetch(testUrl);
        if (response.ok) {
          setTestResults(prev => ({ ...prev, [providerId]: true }));
          toast.success('Gemini API  .');
        } else {
          throw new Error('Invalid API key');
        }
      } else if (providerId === 'openai') {
        testUrl = 'https://api.openai.com/v1/models';
        headers = {
          'Authorization': `Bearer ${key}`
        };
        const response = await fetch(testUrl, { headers });
        if (response.ok) {
          setTestResults(prev => ({ ...prev, [providerId]: true }));
          toast.success('OpenAI API  .');
        } else {
          throw new Error('Invalid API key');
        }
      } else if (providerId === 'claude') {
        // Claude API     
        if (key.startsWith('sk-ant-')) {
          setTestResults(prev => ({ ...prev, [providerId]: true }));
          toast.success('Claude API   .');
        } else {
          throw new Error('Invalid API key format');
        }
      }
    } catch (error) {
      setTestResults(prev => ({ ...prev, [providerId]: false }));
      toast.error(`${providerId} API   .`);
    } finally {
      setTestingProvider(null);
    }
  };

  return (
    <div className="wp-card">
      <div className="wp-card-header">
        <h2 className="wp-card-title flex items-center gap-2">
          <Sparkles className="w-5 h-5" />
          AI API 
        </h2>
      </div>
      
      <div className="wp-card-body space-y-6">
        <Alert>
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>
            AI     AI   API  .
              API    .
                  .
          </AlertDescription>
        </Alert>

        {providers.map((provider) => (
          <div key={provider.id} className="border rounded-lg p-4 space-y-4">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-2">
                <span className="text-2xl">{provider.icon}</span>
                <div>
                  <h3 className="font-semibold">{provider.name}</h3>
                  <p className="text-sm text-gray-500">{provider.description}</p>
                </div>
              </div>
              <a
                href={provider.apiUrl}
                target="_blank"
                rel="noopener noreferrer"
                className="text-sm text-blue-500 hover:underline"
              >
                API   
              </a>
            </div>

            <div className="space-y-3">
              {/* API   */}
              <div>
                <Label htmlFor={`${provider.id}-key`}>API </Label>
                <div className="flex gap-2 mt-1">
                  <div className="relative flex-1">
                    <input
                      id={`${provider.id}-key`}
                      type={showKeys[provider.id] ? 'text' : 'password'}
                      className="w-full px-3 py-2 border rounded-md pr-10"
                      placeholder={
                        provider.id === 'gemini' ? 'AIza...' :
                        provider.id === 'openai' ? 'sk-...' :
                        'sk-ant-...'
                      }
                      value={apiKeys[provider.id]}
                      onChange={(e) => setApiKeys(prev => ({
                        ...prev,
                        [provider.id]: e.target.value
                      }))}
                    />
                    <button
                      type="button"
                      className="absolute right-2 top-1/2 -translate-y-1/2"
                      onClick={() => setShowKeys(prev => ({
                        ...prev,
                        [provider.id]: !prev[provider.id]
                      }))}
                    >
                      {showKeys[provider.id] ? 
                        <EyeOff className="w-4 h-4 text-gray-400" /> : 
                        <Eye className="w-4 h-4 text-gray-400" />
                      }
                    </button>
                  </div>
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleTestKey(provider.id)}
                    disabled={testingProvider === provider.id}
                  >
                    {testingProvider === provider.id ? (
                      <span className="animate-spin"></span>
                    ) : testResults[provider.id] === true ? (
                      <Check className="w-4 h-4 text-green-500" />
                    ) : testResults[provider.id] === false ? (
                      <X className="w-4 h-4 text-red-500" />
                    ) : (
                      ''
                    )}
                  </Button>
                </div>
              </div>

              {/*    */}
              <div>
                <Label htmlFor={`${provider.id}-model`}> </Label>
                <Select 
                  value={defaultModels[provider.id]} 
                  onValueChange={(value) => setDefaultModels(prev => ({
                    ...prev,
                    [provider.id]: value
                  }))}
                >
                  <SelectTrigger id={`${provider.id}-model`} className="mt-1">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    {provider.models.map(model => (
                      <SelectItem key={model.id} value={model.id}>
                        {model.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>
          </div>
        ))}

        <div className="flex justify-end">
          <Button onClick={handleSave} disabled={loading}>
            {loading ? (
              <>
                <span className="animate-spin mr-2"></span>
                 ...
              </>
            ) : (
              <>
                <Save className="mr-2 h-4 w-4" />
                 
              </>
            )}
          </Button>
        </div>
      </div>
    </div>
  );
};

export default AISettings;