import React, { useState, useRef } from 'react';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Label } from '@/components/ui/label';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Progress } from '@/components/ui/progress';
import { Loader2, Sparkles, AlertCircle, X } from 'lucide-react';
import { AIPageGenerator, AIProvider, Block, GenerateOptions, GEMINI_MODELS, OPENAI_MODELS, CLAUDE_MODELS } from '@/services/ai/pageGenerator';
import { AIApiKeyService } from '@/pages/settings/AISettings';
import { useNavigate } from 'react-router-dom';

interface AIPageGeneratorModalProps {
  isOpen: boolean;
  onClose: () => void;
  onGenerate: (blocks: Block[]) => void;
}

const AIPageGeneratorModal: React.FC<AIPageGeneratorModalProps> = ({
  isOpen,
  onClose,
  onGenerate,
}) => {
  const [prompt, setPrompt] = useState('');
  const [template, setTemplate] = useState('landing');
  const [provider, setProvider] = useState<AIProvider['name']>('gemini');
  const [apiKey, setApiKey] = useState('');
  const [selectedModel, setSelectedModel] = useState('gemini-2.5-flash');
  const [isGenerating, setIsGenerating] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [useSavedKey, setUseSavedKey] = useState(true);
  const navigate = useNavigate();
  
  //   
  const [progress, setProgress] = useState(0);
  const [progressMessage, setProgressMessage] = useState('');
  
  // AbortController  ref
  const abortControllerRef = useRef<AbortController | null>(null);
  const generatorRef = useRef<AIPageGenerator | null>(null);
  
  //     API   
  useEffect(() => {
    const savedKey = AIApiKeyService.getKey(provider);
    const savedModel = AIApiKeyService.getDefaultModel(provider);
    
    if (savedKey) {
      setApiKey(savedKey);
      setUseSavedKey(true);
    } else {
      setApiKey('');
      setUseSavedKey(false);
    }
    
    if (savedModel) {
      setSelectedModel(savedModel);
    }
  }, [provider]);

  const templates = [
    { key: 'landing', name: ' ', description: '   ' },
    { key: 'about', name: ' ', description: '   ' },
    { key: 'product', name: ' ', description: '   ' },
    { key: 'blog', name: ' ', description: '  ' },
  ];

  const providers = [
    { key: 'gemini', name: 'Google Gemini ()', requiresKey: true },
    { key: 'openai', name: 'OpenAI (GPT-4)', requiresKey: true },
    { key: 'claude', name: 'Claude', requiresKey: true },
    { key: 'mock', name: ' ', requiresKey: false },
  ];
  
  //    
  const getModelsForProvider = (providerName: string) => {
    switch (providerName) {
      case 'gemini':
        return Object.entries(GEMINI_MODELS);
      case 'openai':
        return Object.entries(OPENAI_MODELS);
      case 'claude':
        return Object.entries(CLAUDE_MODELS);
      default:
        return [];
    }
  };

  const handleGenerate = async () => {
    if (!prompt.trim()) {
      setError('  .');
      return;
    }

    const selectedProvider = providers.find(p => p.key === provider);
    
    // API   -      
    let finalApiKey = apiKey;
    if (useSavedKey && selectedProvider?.requiresKey) {
      const savedKey = AIApiKeyService.getKey(provider);
      if (savedKey) {
        finalApiKey = savedKey;
      }
    }
    
    if (selectedProvider?.requiresKey && !finalApiKey) {
      setError('API  .  API    .');
      return;
    }

    setIsGenerating(true);
    setError(null);
    setProgress(0);
    setProgressMessage(' ...');

    // AbortController 
    abortControllerRef.current = new AbortController();

    try {
      const generator = new AIPageGenerator({
        name: provider === 'mock' ? 'openai' : provider as AIProvider['name'],
        apiKey: provider === 'mock' ? undefined : finalApiKey,
        model: provider === 'mock' ? undefined : selectedModel,
      });
      
      generatorRef.current = generator;

      //  
      const onProgress = (progress: number, message: string) => {
        setProgress(progress);
        setProgressMessage(message);
      };

      const options: GenerateOptions = {
        prompt,
        template: template as any,
        onProgress,
        signal: abortControllerRef.current.signal,
      };

      const blocks = provider === 'mock' 
        ? await generator.generateBlocks(options)
        : await generator.generateBlocks(options);

      onGenerate(blocks);
      onClose();
      
      //   
      setPrompt('');
      setApiKey('');
      setProgress(0);
      setProgressMessage('');
    } catch (err: any) {
      if (err.message === ' ') {
        setError(' .');
      } else {
        setError(err instanceof Error ? err.message : 'AI    .');
      }
    } finally {
      setIsGenerating(false);
      abortControllerRef.current = null;
      generatorRef.current = null;
    }
  };

  const handleCancel = () => {
    if (abortControllerRef.current) {
      abortControllerRef.current.abort();
    }
    if (generatorRef.current) {
      generatorRef.current.cancel();
    }
    setIsGenerating(false);
    setProgress(0);
    setProgressMessage('');
  };

  const handleClose = () => {
    if (isGenerating) {
      handleCancel();
    }
    onClose();
  };

  const examplePrompts = {
    landing: '     . , ,   .',
    about: '      .',
    product: 'AI       .',
    blog: '2025       .',
  };

  return (
    <Dialog open={isOpen} onOpenChange={handleClose}>
      <DialogContent className="sm:max-w-[600px]">
        <DialogHeader>
          <DialogTitle className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <Sparkles className="h-5 w-5 text-purple-500" />
              AI  
            </div>
            {isGenerating && (
              <Button
                variant="ghost"
                size="sm"
                onClick={handleCancel}
                className="text-red-500 hover:text-red-700"
              >
                <X className="h-4 w-4 mr-1" />
                
              </Button>
            )}
          </DialogTitle>
          <DialogDescription>
            AI    .
          </DialogDescription>
        </DialogHeader>

        {isGenerating ? (
          //   
          <div className="space-y-4 py-8">
            <div className="text-center space-y-2">
              <Loader2 className="h-8 w-8 animate-spin mx-auto text-purple-500" />
              <p className="text-sm font-medium">{progressMessage}</p>
              <p className="text-xs text-muted-foreground"> ...</p>
            </div>
            
            <div className="space-y-2">
              <Progress value={progress} className="h-2" />
              <p className="text-center text-sm text-muted-foreground">{progress}%</p>
            </div>

            <div className="text-center">
              <Button
                variant="outline"
                size="sm"
                onClick={handleCancel}
                className="text-red-600 border-red-200 hover:bg-red-50"
              >
                <X className="h-4 w-4 mr-1" />
                 
              </Button>
            </div>
          </div>
        ) : (
          //  
          <div className="space-y-4 py-4">
            {/*   */}
            <div className="space-y-2">
              <Label htmlFor="template"></Label>
              <Select value={template} onValueChange={setTemplate}>
                <SelectTrigger id="template">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  {templates.map((t) => (
                    <SelectItem key={t.key} value={t.key}>
                      <div>
                        <div className="font-medium">{t.name}</div>
                        <div className="text-xs text-muted-foreground">{t.description}</div>
                      </div>
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            {/* AI   */}
            <div className="space-y-2">
              <Label htmlFor="provider">AI </Label>
              <Select value={provider} onValueChange={(v) => {
                setProvider(v as AIProvider['name']);
                //      
                if (v === 'gemini') setSelectedModel('gemini-2.5-flash');
                else if (v === 'openai') setSelectedModel('gpt-4-turbo');
                else if (v === 'claude') setSelectedModel('claude-3-sonnet-20240229');
              }}>
                <SelectTrigger id="provider">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  {providers.map((p) => (
                    <SelectItem key={p.key} value={p.key}>
                      <div className="flex items-center gap-2">
                        <span>{p.name}</span>
                        {!p.requiresKey && (
                          <span className="text-xs bg-green-100 text-green-800 px-2 py-0.5 rounded">
                            
                          </span>
                        )}
                      </div>
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            
            {/*   () */}
            {provider !== 'mock' && (
              <div className="space-y-2">
                <Label htmlFor="model"> </Label>
                <Select value={selectedModel} onValueChange={setSelectedModel}>
                  <SelectTrigger id="model">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    {getModelsForProvider(provider).map(([key, name]) => (
                      <SelectItem key={key} value={key}>
                        <div className="text-sm">
                          <div>{key}</div>
                          <div className="text-xs text-muted-foreground">{name}</div>
                        </div>
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            )}

            {/* API   */}
            {providers.find(p => p.key === provider)?.requiresKey && (
              <div className="space-y-2">
                <Label>API  </Label>
                {AIApiKeyService.getKey(provider) ? (
                  <div className="p-3 bg-green-50 border border-green-200 rounded-md">
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-2">
                        <Check className="w-4 h-4 text-green-600" />
                        <span className="text-sm text-green-800"> API   </span>
                      </div>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => navigate('/settings/ai')}
                      >
                         
                      </Button>
                    </div>
                  </div>
                ) : (
                  <div className="space-y-2">
                    <Alert variant="destructive">
                      <AlertCircle className="h-4 w-4" />
                      <AlertDescription>
                        API   .
                        <Button
                          variant="link"
                          size="sm"
                          className="ml-2 p-0 h-auto"
                          onClick={() => navigate('/settings/ai')}
                        >
                           
                        </Button>
                      </AlertDescription>
                    </Alert>
                    
                    <div className="flex items-center gap-2">
                      <input
                        type="checkbox"
                        id="use-temp-key"
                        checked={!useSavedKey}
                        onChange={(e) => setUseSavedKey(!e.target.checked)}
                      />
                      <Label htmlFor="use-temp-key" className="text-sm cursor-pointer">
                         API  
                      </Label>
                    </div>
                    
                    {!useSavedKey && (
                      <div>
                        <input
                          id="apiKey"
                          type="password"
                          className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 text-sm"
                          placeholder={`${provider === 'openai' ? 'sk-...' : provider === 'claude' ? 'sk-ant-...' : 'AIza...'}`}
                          value={apiKey}
                          onChange={(e) => setApiKey(e.target.value)}
                        />
                        <p className="text-xs text-muted-foreground mt-1">
                              .
                        </p>
                      </div>
                    )}
                  </div>
                )}
              </div>
            )}

            {/*   */}
            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <Label htmlFor="prompt">  </Label>
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => setPrompt(examplePrompts[template as keyof typeof examplePrompts])}
                >
                   
                </Button>
              </div>
              <Textarea
                id="prompt"
                placeholder="  ?  ..."
                value={prompt}
                onChange={(e) => setPrompt(e.target.value)}
                rows={5}
                className="resize-none"
              />
              <p className="text-xs text-muted-foreground">
                 ,  ,           .
              </p>
            </div>

            {/*   */}
            {error && (
              <Alert variant="destructive">
                <AlertCircle className="h-4 w-4" />
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}
          </div>
        )}

        {!isGenerating && (
          <DialogFooter>
            <Button variant="outline" onClick={handleClose}>
              
            </Button>
            <Button onClick={handleGenerate}>
              <Sparkles className="mr-2 h-4 w-4" />
               
            </Button>
          </DialogFooter>
        )}
      </DialogContent>
    </Dialog>
  );
};

export default AIPageGeneratorModal;