import { User as BaseUser } from '@o4o/types';

export interface BusinessInfo {
  businessName?: string;
  businessType?: string;
  businessPhone?: string;
  businessAddress?: string;
  businessRegistration?: string;
  businessNumber?: string;
  representativeName?: string;
  contactPhone?: string;
}

export interface User extends Omit<BaseUser, 'role' | 'status'> {
  // Unified fields
  fullName?: string;
  firstName?: string;
  lastName?: string;
  lastLoginAt?: string | Date;
  lastLogin?: Date;
  last_login?: Date;
  phone?: string;
  businessInfo?: BusinessInfo;
  createdAt: string | Date;
  created_at?: Date;
  updatedAt?: string | Date;
  updated_at?: Date;
  isEmailVerified?: boolean;
  isActive?: boolean;
  avatar?: string;
  role: UserRole;
  roles?: UserRole[];
  status: UserStatus;
}

export type UserRole = 'admin' | 'moderator' | 'user' | 'guest' | 'vendor' | 'customer' | 'affiliate' | 'partner' | 'business' | 'seller' | 'supplier' | 'manager' | 'retailer';

export type UserStatus = 'active' | 'inactive' | 'pending' | 'suspended' | 'deleted' | 'approved' | 'rejected';

export const ROLE_LABELS: Record<UserRole, string> = {
  admin: '',
  moderator: '',
  user: ' ',
  guest: '',
  business: '', 
  affiliate: '',
  partner: '',
  customer: '',
  seller: '',
  supplier: '',
  vendor: '',
  manager: '',
  retailer: ''
};

export const roleDisplayNames: Record<UserRole, string> = ROLE_LABELS;

// Additional exports for UserApi
export interface UserFilters {
  role?: UserRole | 'all';
  status?: UserStatus | 'all';
  search?: string;
  dateFrom?: string;
  dateTo?: string;
  businessType?: string;
}

export interface UserBulkAction {
  action: 'activate' | 'deactivate' | 'delete' | 'change_role' | 'approve' | 'reject';
  userIds: string[];
  newRole?: UserRole;
  reason?: string;
}

export interface UserFormData {
  email: string;
  name: string;
  role: UserRole;
  status: UserStatus;
  password?: string;
  phone?: string;
  businessInfo?: {
    businessName?: string;
    businessType?: string;
    businessPhone?: string;
    businessAddress?: string;
    businessRegistration?: string;
    businessNumber?: string;
    representativeName?: string;
    contactPhone?: string;
  };
  sendWelcomeEmail?: boolean;
}

export interface UserStats {
  total: number;
  active: number;
  inactive: number;
  pending: number;
  approved: number;
  rejected: number;
  suspended: number;
  byRole: Record<UserRole, number>;
}

export interface UserActivityLog {
  id: string;
  userId: string;
  action: string;
  details: string;
  timestamp: Date;
  ipAddress?: string;
}

export const STATUS_LABELS = {
  pending: ' ',
  active: '',
  inactive: '',
  approved: '',
  rejected: '',
  suspended: ''
} as const;

export const BUSINESS_TYPES = {
  retail: '',
  wholesale: '',
  manufacturing: '',
  service: '',
  technology: '',
  consulting: '',
  other: ''
} as const;

export const BUSINESS_TYPE_OPTIONS = Object.entries(BUSINESS_TYPES).map(([key, value]) => ({
  value: key,
  label: value
}));
