// Request deduplication
const pendingRequests = new Map<string, AbortController>();

const createRequestKey = (method: string, url: string, data?: any): string => {
  // Remove _requestId from data for key generation
  const cleanData = data ? { ...data } : {};
  delete cleanData._requestId;
  return `${method}-${url}-${JSON.stringify(cleanData)}`;
};

const cancelPendingRequest = (key: string) => {
  const controller = pendingRequests.get(key);
  if (controller) {
    controller.abort();
    pendingRequests.delete(key);
  }
};

/**
 *  API
 */

export const postApi = {
  //   (/api/posts :   409)
  create: async (data: CreatePostRequest): Promise<PostResponse> => {
    // Cancel any pending request with same data
    const requestKey = createRequestKey('POST', '/posts', data);
    cancelPendingRequest(requestKey);
    
    // Create new abort controller
    const abortController = new AbortController();
    pendingRequests.set(requestKey, abortController);
    
    try {
      // Remove _requestId from actual request
      const requestData = { ...data };
      delete (requestData as any)._requestId;
      
      
      const response = await apiClient.post('/posts', requestData, {
        signal: abortController.signal
      } as AxiosRequestConfig);
      
      
      pendingRequests.delete(requestKey);
      return { success: true, data: response.data };
    } catch (error: any) {
      pendingRequests.delete(requestKey);
      
      // Don't report aborted requests as errors
      if (error.name === 'CanceledError' || error.code === 'ERR_CANCELED') {
        return { success: false, error: 'Request canceled' };
      }
      
      return { 
        success: false, 
        error: error.response?.data?.message || `Failed to create post (${error.response?.status})` 
      };
    }
  },

  //   (/api/posts)
  get: async (id: string): Promise<PostResponse> => {
    try {
      const response = await apiClient.get(`/posts/${id}`);
      return { success: true, data: response.data };
    } catch (error: any) {
      return { 
        success: false, 
        error: error.response?.data?.message || 'Failed to get post' 
      };
    }
  },

  //   (/api/posts)
  update: async (data: UpdatePostRequest): Promise<PostResponse> => {
    const { id, ...updateData } = data;
    
    // Cancel any pending request with same data
    const requestKey = createRequestKey('PUT', `/posts/${id}`, updateData);
    cancelPendingRequest(requestKey);
    
    // Create new abort controller
    const abortController = new AbortController();
    pendingRequests.set(requestKey, abortController);
    
    try {
      // Remove _requestId from actual request
      const requestData = { ...updateData };
      delete (requestData as any)._requestId;
      
      const response = await apiClient.put(`/posts/${id}`, requestData, {
        signal: abortController.signal
      } as AxiosRequestConfig);
      
      pendingRequests.delete(requestKey);
      return { success: true, data: response.data };
    } catch (error: any) {
      pendingRequests.delete(requestKey);
      
      // Don't report aborted requests as errors
      if (error.name === 'CanceledError' || error.code === 'ERR_CANCELED') {
        return { success: false, error: 'Request canceled' };
      }
      
      return { 
        success: false, 
        error: error.response?.data?.message || 'Failed to update post' 
      };
    }
  },
  
  //  (/api/posts/:id/autosave)
  autoSave: async (id: string, data: { title?: string; content?: any; excerpt?: string }): Promise<PostResponse> => {
    try {
      const response = await apiClient.post(`/posts/${id}/autosave`, data);
      return { success: true, data: response.data };
    } catch (error: any) {
      return { 
        success: false, 
        error: error.response?.data?.message || 'Failed to autosave' 
      };
    }
  },

  //   (V1 API )
  delete: async (id: string): Promise<{ success: boolean; error?: string }> => {
    try {
      await apiV1Client.delete(`/posts/${id}`);
      return { success: true };
    } catch (error: any) {
      return { 
        success: false, 
        error: error.response?.data?.message || 'Failed to delete post' 
      };
    }
  },

  //   (V1 API )
  publish: async (id: string): Promise<PostResponse> => {
    try {
      const response = await apiV1Client.post(`/posts/${id}/publish`);
      return { success: true, data: response.data };
    } catch (error: any) {
      return { 
        success: false, 
        error: error.response?.data?.message || 'Failed to publish post' 
      };
    }
  },

  //   (V1 API )
  saveDraft: async (data: CreatePostRequest | UpdatePostRequest): Promise<PostResponse> => {
    try {
      const endpoint = 'id' in data ? `/posts/${data.id}/draft` : '/posts/draft';
      
      if (import.meta.env.DEV) {
        // Dev-only: draft request
      }
      
      const response = await apiV1Client.post(endpoint, data);
      
      if (import.meta.env.DEV) {
        // Dev-only: draft success
      }
      
      return { success: true, data: response.data };
    } catch (error: any) {
      return { 
        success: false, 
        error: error.response?.data?.message || `Failed to save draft (${error.response?.status})` 
      };
    }
  },

  //    (V1 API )
  list: async (params?: {
    page?: number;
    pageSize?: number;
    status?: string;
    author?: string;
    category?: string;
    tag?: string;
    search?: string;
  }): Promise<PostListResponse> => {
    try {
      const response = await apiV1Client.get('/posts', { params });
      return { success: true, data: response.data };
    } catch (error: any) {
      return { 
        success: false, 
        error: error.response?.data?.message || 'Failed to list posts' 
      };
    }
  },
};

/**
 *  API
 */
export const mediaApi = {
  //   (V1 API )
  upload: async (file: File, onProgress?: (progress: number) => void): Promise<MediaUploadResponse> => {
    try {
      const formData = new FormData();
      formData.append('file', file);

      const response = await apiV1Client.post('/content/media/upload', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        onUploadProgress: (progressEvent: any) => {
          if (progressEvent.total) {
            const progress = Math.round((progressEvent.loaded * 100) / progressEvent.total);
            onProgress?.(progress);
          }
        },
      } as any);

      return { success: true, data: response.data };
    } catch (error: any) {
      return { 
        success: false, 
        error: error.response?.data?.message || 'Failed to upload media' 
      };
    }
  },

  //    (V1 API )
  list: async (params?: {
    page?: number;
    pageSize?: number;
    type?: string;
  }): Promise<{ success: boolean; data?: Media[]; error?: string }> => {
    try {
      const response = await apiV1Client.get('/media', { params });
      return { success: true, data: response.data };
    } catch (error: any) {
      return { 
        success: false, 
        error: error.response?.data?.message || 'Failed to list media' 
      };
    }
  },

  //   (V1 API )
  delete: async (id: string): Promise<{ success: boolean; error?: string }> => {
    try {
      await apiV1Client.delete(`/content/media/${id}`);
      return { success: true };
    } catch (error: any) {
      return { 
        success: false, 
        error: error.response?.data?.message || 'Failed to delete media' 
      };
    }
  },
};

